  display: grid;
  grid-template-columns: auto 1fr;
  padding: .5rem;
  height: 2rem;
}

.app-file-area.app-file-area--active + .app-mapper {
  height: auto;
}

.mapper-list {
  list-style: none;
  margin: 0;
  padding: 0;
  min-width: calc(11.5em + 2px);
  max-height: 100%;
  overflow: hidden;
}

.mapper-item {
  margin: .75rem;
  border-radius: 3px;
  border: 1px solid #ccc;
  background-color: #fff;
  -webkit-transition: border-color .25s ease;
  -o-transition: border-color .25s ease;
  transition: border-color .25s ease;
  display: -ms-flexbox;
  display: flex;
  height: 2.25rem;
  line-height: 1.5;
  position: relative;
}

.item--header {
  width: 10em;
  padding: .25rem .5rem;
  overflow: hidden;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.item--column {
  -ms-flex: 1 1;
      flex: 1 1;
  padding: .25rem .25rem .25rem 1rem;
  background-color: #eee;
  font-weight: 600;
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%, 0 50%, 0 0);
          clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%, 0 50%, 0 0);
  -webkit-transition: .25s ease;
  -o-transition: .25s ease;
  transition: .25s ease;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: start;
