.state--good {
  border-color: #8BC34A;
}
.state--good .item--column {
  background-color: #8BC34A;
  color: #fff;
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%, .375rem 50%, 0 0);
          clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%, .375rem 50%, 0 0);
}

.app-mapper--active .mapper-item:hover {
  border-color: #1E88E5;
}

.app-mapper--active .mapper-item:hover .item--column {
  background-color: #1E88E5;
  color: #fff;
}

.state--shadow {
  opacity: .5;
}

.app-file-drop {
  margin: 1rem;
  border: .25rem dashed #bbb;
  border-radius: .75rem;
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
