.app-file-widget button {
  border: 0;
  background-color: transparent;
  font-size: 1rem;
  font-family: 'Source Sans Pro', sans-serif;
  color: #1E88E5;
}

.menu-button {
  border: 0;
  background-color: transparent;
  text-indent: -999px;
  overflow: hidden;
  width: 1.5rem;
  height: 1.5rem;
  padding: 0;
  margin-left: auto;
