}

.column-menu li:hover {
  background-color: #eee;
}

.column-menu li.disabled {
  color: #bbb;
  cursor: default;
}

.column-menu li.disabled:hover {
  background-color: transparent;
}

.default-value {
  font-weight: 400;
  opacity: .75;
}

.list-header {
  font-size: 1rem;
  margin: .25rem .75rem;
  font-weight: 600;
  font-size: .875rem;
  color: #888;
}


